.Header{
    background-image: linear-gradient(0deg,rgba(0,0,0) 0,transparent 10%,rgba(0,0,0)), url(header.jpg) ;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;
    border-bottom: 8px solid #222;
}
.logo{
    background-color: transparent;
    width: 300px;
    height: 70px;
    max-width: 100%;
}
nav button{
    background-color: #e50914;
    color: #FFFFFF;
    border-radius: 4px;
    text-transform: capitalize;
    font-size: 20px;
    padding: .5em;
    border: none;
    float: right;
    margin-top: 1.5em;
}
.input-section{
    text-align: center;
    padding: 6em;
    color: #FFFFFF;
    margin: 5em 0 5em 0;
}
.input-section h1{
    font-size: 50px;
    margin: .5em 0 .5em 0;
}
.input-section h2{
    font-size: 30px;
    margin: .5em 0 .5em 0;
}
.input-section p{
    font-size: 22px;
    margin: .5em 0 1em 0;
}
.input-start{
    padding: 22px;
    width: 30%;
    border-radius: 2px 0 0 2px;
    border: none;
    vertical-align: middle;
    cursor: pointer;
}
.submit-btn{
    padding: .75em;
    background-color: #e50914;
    color: #FFFFFF;
    text-transform: capitalize;
    font-size: 20px;
    border: none;
    vertical-align: middle;
    border-radius: 0 2px 2px 0;
    cursor: pointer;
}
.angle-right-svg{
    filter: brightness(0) invert(1);
    width: 30px;
    height: 30px;
    vertical-align: middle;
}
.Card{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    background-color: #000;
    padding: 6em;
    color: #FFFFFF;
    border-bottom: 8px solid #222;
}
.card-text h1{
    font-size: 3rem;
    margin: .5em 0 .5em 0;
}
.card-text p{
    font-size: 1.5rem;
    line-height: 1.3;
}
.tv-image{
    max-width: 100%;
}
.Faq-section{
    background-color: #000;
    text-align: center;
    padding: 3em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: #FFFFFF;
    border-bottom: 8px solid #222;
}
.Faq-section p{
    font-size: 20px;
    margin: .6rem 0;
}
.Faq-section h1{
    font-size: 3rem;
    text-transform: capitalize;
    margin-bottom: .3em;
}
.faq-options{
    margin: auto;
    width: 65%;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
}
.faq-options h2{
    background-color: #303030;
    padding: .75em;
    text-align: left;
    margin-top: 8px;
    font-size: 1.7rem;
    font-weight: 400;
    cursor: pointer;
}
.panel{
    margin-top: 1px;
    display: none;
    overflow: hidden;
}
.panel.show:target{
    height: auto;
}
.panel.show{
    margin-top: 1px;
    display: block;
    animation: slide-down .2s linear both;
    transition: 0.3s ease-in-out;
}
@keyframes slide-down{
    0%{
        transform: translateY(-10px);
    }
    100%{
        transform: translateY(0px);
    }
}
.cross, .plus{
    width: 30px;
    height: 30px;
    filter: brightness(0) invert(1);
    float: right;
}
.faq-input{
    margin: 2rem 0;
}
.Footer{
    background-color: #000;
    color: #737373;
    padding: 4em;
}
.Footer h3{
    margin: 1em 0;
    font-weight: 400;
    cursor: pointer;
}
.services-list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
li{
    list-style-type: none;
    margin: 1em 0;
    cursor: pointer;
}
.Footer h3:hover, li:hover{
    text-decoration: underline;
}
.Footer h4{
    color: #737373;
    font-weight: 400;
    margin-top: 1em;
}

@media screen and (max-width: 950px){
    .logo{
        width: 170px;
        height: 70px;
    }
    nav button{
        margin: .5em;
    }
    .input-section{
        padding: 3em;
    }
    .input-section h1{
        font-size: 2rem;
    }
    .Card{
        grid-template-columns: 1fr;  
        padding: 3em;     
    }
    .card-text h1{
        font-size: 2rem;
    }
    .card-text p{
        font-size: 1em;
    }
    .tv-image{
        margin: auto;
    }
    .Faq-section{
        padding: 2em;
    }
    .Faq-section h1{
        font-size: 2rem;
    }
    .faq-options{
        width: 100%;
    }
    .faq-options h2{
        font-size: 1.2rem;
    }
    .services-list{
        grid-template-columns: 1fr 1fr;
    }
    .services{
        margin-bottom: 0;
    }
    .input-start{
        width: 80%;
        margin-bottom: 10px;
    }
    .Footer{
        padding: 2em;
    }
    .services{
        margin-left: -40px;
    }
}
@media screen and (max-width: 400px){
    .input-section{
        padding: 1em;
    }
    .logo{
        width: 150px;
    }
    .Faq-section{
        padding: 0;
    }
    .plus, .cross{
        width: 20px;
        height: 20px;
    }
    .services{
        margin-left: -40px;
    }
}